/// <reference types="../../declaration.d.ts" />

import React, { useEffect, useState } from 'react';
import styles from './VUMeterView.module.scss';
import { useAppContext } from '../../contexts/AppContextProvider';
import { VUMeter, VUMeterConfig } from 'now-playing-common';
import VUMeterPanel from '../../common/vumeter/VUMeterPanel';
import { random } from 'lodash';

export interface VUMeterViewProps {
  template?: string;
  meter?: string;
}

function VUMeterView(props: VUMeterViewProps) {
  const { pluginInfo } = useAppContext();
  const [ config, setConfig ] = useState<VUMeter | null>(null);
  const [ meterPanelSize, setMeterPanelSize ] = useState({width: window.innerWidth, height: window.innerHeight});
  const appUrl = pluginInfo?.appUrl || null;
  const { template, meter } = props;

  useEffect(() => {
    const refreshMeterPanel = () => {
      setMeterPanelSize({
        width: window.innerWidth,
        height: window.innerHeight
      });
    };

    window.addEventListener('resize', refreshMeterPanel);

    return () => {
      window.removeEventListener('resize', refreshMeterPanel);
    };
  }, []);

  useEffect(() => {
    const loadConfig = async () => {
      const url = template ? `${appUrl}/vumeter/${template}` : `${appUrl}/vumeter`;
      try {
        const res = await fetch(url);
        const configRes = (await res.json()) as VUMeterConfig;
        if (!configRes.meters || configRes.meters.length === 0) {
          setConfig(null);
          return;
        }
        const meters = configRes.meters;
        let targetMeter: VUMeter | null = null;
        if (template && meter) {
          targetMeter = meters.find((m) => m.name === meter) || null;
        }
        if (!targetMeter) {
          targetMeter = meters[random(0, meters.length - 1)] || null;
        }
        setConfig(targetMeter);
      }
      catch (error) {
        // TODO: pass error to VUMeterPanel
        setConfig(null);
      }
    };

    if (!appUrl) {
      setConfig(null);
    }
    else {
      loadConfig();
    }
  }, [ template, meter, appUrl ]);

  if (!config) {
    return;
  }

  const screenStyles = {} as React.CSSProperties;
  if (config.images.screenBackground) {
    screenStyles['--background'] = `url("${config.images.screenBackground}")`;
  }

  return (
    <div className={styles.Layout} style={screenStyles}>
      <VUMeterPanel
        config={config}
        size={meterPanelSize}
      />
    </div>
  );
}

export default VUMeterView;
